import { searchIcon } from '../../settings/settings';

const componentClassName = 'search-bar';
const searchBarFromClassName = `${componentClassName}__form`;
const searchBarIconClassName = `${componentClassName}__icon`;
const searchBarInputClassName = `${componentClassName}__input`;

const searchBar = ({ onInputChange, searchQuery }) => {
  const component = document.createElement('div');
  component.classList.add(componentClassName);

  const searchBarForm = document.createElement('form');
  searchBarForm.classList.add(searchBarFromClassName);

  const searchBarIcon = document.createElement('label');
  searchBarIcon.htmlFor = searchBarInputClassName;
  searchBarIcon.classList.add(searchBarIconClassName, ...searchIcon);

  const searchBarInput = document.createElement('input');
  searchBarInput.classList.add(searchBarInputClassName);
  searchBarInput.id = searchBarInputClassName;
  searchBarInput.type = 'text';
  searchBarInput.placeholder = 'Search...';
  searchBarInput.value = searchQuery;
  searchBarInput.addEventListener('input', onInputChange);

  searchBarForm.appendChild(searchBarIcon);
  searchBarForm.appendChild(searchBarInput);
  component.appendChild(searchBarForm);

  return component;
};

export default searchBar;
