import {
  API_KEY,
  CHUNK_SIZE,
} from '../settings/settings';

const fetchChunkInfo = ({ searchQuery, pageToken }) => fetch(`https://www.googleapis.com/youtube/v3/search?key=${API_KEY}&part=id${pageToken ? `&pageToken=${pageToken}` : ''}&q=${searchQuery}&type=video&maxResults=${CHUNK_SIZE}`)
  .then(response => response.json())
  .then(({ prevPageToken, nextPageToken, items }) => ({
    videoIds: items.map(item => item.id.videoId).join(','),
    prevPageToken,
    nextPageToken,
  }))
  .then(pageInfo => pageInfo);

const parseVideoItem = (item) => {
  const { id } = item;
  const {
    title,
    description,
    channelTitle,
    publishedAt,
    thumbnails,
  } = item.snippet;
  const { viewCount } = item.statistics;

  const link = `https://www.youtube.com/watch?v=${id}`;

  return {
    link,
    title,
    description,
    channelTitle,
    publishedAt,
    viewCount,
    thumbnails: thumbnails.medium,
  };
};

const fetchChunkVideos = ({ videoIds, ...rest }) => fetch(`https://www.googleapis.com/youtube/v3/videos?key=${API_KEY}&id=${videoIds}&part=snippet,statistics`)
  .then(response => response.json())
  .then(({ items }) => ({
    items: items.map(parseVideoItem),
    ...rest,
  }));

const fetchVideoItems = searchQuery => fetchChunkInfo(searchQuery)
  .then(fetchChunkVideos);

export default fetchVideoItems;
