import searchBarRenderer from './components/searchBar/searchBar';
import sliderRenderer from './components/slider/slider';
import pagingRenderer from './components/paging/paging';

import fetchVideoItems from './modules/fetchVideoItems';
import getNumberOfBlocks from './modules/getNumberOfBlocks';
import shouldPreloadChunk from './modules/shouldPreloadChunk';

import { CHUNK_SIZE, PRELOAD_ITEMS_RATIO, animationClasses } from './settings/settings';

class App {
  constructor() {
    this.state = {
      offset: 0,
      searchQuery: '',
      pageSize: getNumberOfBlocks(),
      isLoading: false,
      itemsQueue: [],
      animationClass: animationClasses.getFromBottom,
    };

    this.setState = this.setState.bind(this);
    this.loadPrevPage = this.loadPrevPage.bind(this);
    this.loadNextPage = this.loadNextPage.bind(this);
    this.rotateItemsQueue = this.rotateItemsQueue.bind(this);
    this.loadVideoItems = this.loadVideoItems.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
    this.onWindowResize = this.onWindowResize.bind(this);
    this.onMouseDown = this.onMouseDown.bind(this);
    this.onMouseUp = this.onMouseUp.bind(this);
    this.onTouchStart = this.onTouchStart.bind(this);
    this.onTouchEnd = this.onTouchEnd.bind(this);
    this.shouldPreloadPrevChunck = this.shouldPreloadPrevChunck.bind(this);
    this.shouldPreloadNextChunk = this.shouldPreloadNextChunk.bind(this);
    this.render = this.render.bind(this);
  }

  init() {
    this.component = document.createElement('main');
    this.component.classList.add('main-view');
    window.addEventListener('resize', this.onWindowResize);

    const searchBar = searchBarRenderer;

    this.component.appendChild(searchBar({
      onInputChange: this.onInputChange,
      searchQuery: this.state.searchQuery,
    }));

    this.sliderRenderFunction = sliderRenderer;

    this.slider = this.sliderRenderFunction({
      onMouseDown: this.onMouseDown,
      onMouseUp: this.onMouseUp,
      onTouchStart: this.onTouchStart,
      onTouchEnd: this.onTouchEnd,
      animationClass: this.state.animationClass,
      videoItems: this.getCurrentPageItems(),
    });

    this.component.appendChild(this.slider);

    this.pagingRenderFunction = pagingRenderer;

    this.paging = this.pagingRenderFunction({
      loadPrevPage: this.loadPrevPage,
      loadNextPage: this.loadNextPage,
      currentPage: this.getCurrentPageNumber(),
    });

    this.component.appendChild(this.paging);

    this.loadVideoItems(null, chunk => this.setState({
      currentChunk: { chunkNumber: 0, ...chunk },
      isLoading: false,
    }));
  }

  setState(newState) {
    this.state = {
      ...this.state,
      ...newState,
    };

    this.render();
  }

  loadPrevChunk() {
    const { currentChunk } = this.state;
    const { chunkNumber, prevPageToken } = currentChunk;

    if (prevPageToken) {
      this.loadVideoItems(prevPageToken, chunk => this.setState({
        prevChunk: { chunkNumber: chunkNumber - 1, ...chunk },
        isLoading: false,
      }));
    }
  }

  loadNextChunk() {
    const { currentChunk } = this.state;
    const { chunkNumber, nextPageToken } = currentChunk;

    if (nextPageToken) {
      this.loadVideoItems(nextPageToken, chunk => this.setState({
        nextChunk: { chunkNumber: chunkNumber + 1, ...chunk },
        isLoading: false,
      }));
    }
  }

  shouldPreloadPrevChunck(offset) {
    const { prevChunk, currentChunk: { prevPageToken } } = this.state;
    return shouldPreloadChunk(
      prevChunk,
      prevPageToken,
      offset,
      1 - PRELOAD_ITEMS_RATIO,
      (left, right) => left < right,
    );
  }

  shouldPreloadNextChunk(offset) {
    const { nextChunk, currentChunk: { nextPageToken } } = this.state;
    return shouldPreloadChunk(
      nextChunk,
      nextPageToken,
      offset,
      PRELOAD_ITEMS_RATIO,
      (left, right) => left > right,
    );
  }

  loadPrevPage() {
    const {
      offset,
      pageSize,
      currentChunk,
      prevChunk,
      itemsQueue,
      isLoading,
    } = this.state;

    if (isLoading) {
      this.setState({
        itemsQueue: [...itemsQueue, this.loadNextPage],
        isLoading: true,
      });
      return;
    }

    const chunkStartOffset = currentChunk.chunkNumber * CHUNK_SIZE;
    const newOffset = offset - pageSize;
    if (newOffset >= chunkStartOffset) {
      const shouldPreloadItems = this.shouldPreloadPrevChunck(newOffset);
      if (shouldPreloadItems) {
        this.loadPrevChunk();
      }
      this.setState({ offset: newOffset, animationClass: animationClasses.getFromLeft });
      return;
    }
    if (prevChunk) {
      this.setState({
        prevChunk: null,
        currentChunk: prevChunk,
        nextChunk: currentChunk,
        offset: newOffset,
        animationClass: animationClasses.getFromLeft,
      });
    } else {
      this.setState({ offset: 0, animationClass: animationClasses.getFromLeft });
    }
  }

  loadNextPage() {
    const {
      offset,
      pageSize,
      currentChunk,
      nextChunk,
      itemsQueue,
      isLoading,
    } = this.state;

    if (isLoading) {
      this.setState({
        itemsQueue: [...itemsQueue, this.loadNextPage],
        isLoading: true,
      });
      return;
    }

    const chunkStartOffset = (currentChunk.chunkNumber + 1) * CHUNK_SIZE;
    const newOffset = offset + pageSize;
    if (newOffset < chunkStartOffset) {
      const shouldPreloadItems = this.shouldPreloadNextChunk(newOffset);
      if (shouldPreloadItems) {
        this.loadNextChunk();
      }
      this.setState({ offset: newOffset, animationClass: animationClasses.getFromRight });
    } else if (nextChunk) {
      this.setState({
        prevChunk: currentChunk,
        currentChunk: nextChunk,
        nextChunk: null,
        offset: newOffset,
        animationClass: animationClasses.getFromRight,
      });
    }
  }

  rotateItemsQueue() {
    const { itemsQueue, isLoading } = this.state;

    if (!itemsQueue.length || isLoading) {
      return;
    }

    const [rotateCallback, ...rest] = itemsQueue;
    this.setState({ itemsQueue: rest });
    rotateCallback();
    this.rotateItemsQueue();
  }

  loadVideoItems(pageToken, callback) {
    const { searchQuery } = this.state;
    const result = fetchVideoItems({ searchQuery, pageToken })
      .then(callback)
      .then(this.rotateItemsQueue)
      .then(this.render)
      .catch((err) => { throw err; });
    this.setState({ isLoading: true });
    return result;
  }

  onInputChange(event) {
    this.setState({
      searchQuery: event.target.value,
      prevChunk: null,
      nextChunk: null,
      currentChunk: null,
    });
    this.setState({
      offset: 0,
      animationClass: animationClasses.getFromBottom,
    });
    this.loadVideoItems(null, chunk => this.setState({
      currentChunk: { chunkNumber: 0, ...chunk },
      isLoading: false,
    }));
  }

  onWindowResize() {
    const { pageSize } = this.state;
    const newPageSize = getNumberOfBlocks();
    if (pageSize !== newPageSize) {
      this.setState({ pageSize: newPageSize });
      this.render();
    }
  }

  onMouseDown(e) {
    const { clientX } = e;
    this.clientX = clientX;
  }

  onMouseUp(e) {
    const { clientX } = e;
    if (this.clientX - clientX < 0) {
      this.loadPrevPage();
    }
    if (this.clientX - clientX > 0) {
      this.loadNextPage();
    }
  }

  onTouchStart(e) {
    const { changedTouches } = e;
    this.clientX = changedTouches[0].clientX;
  }

  onTouchEnd(e) {
    const { changedTouches } = e;
    const { clientX } = changedTouches[0];
    if (this.clientX - clientX < 0) {
      this.loadPrevPage();
    }
    if (this.clientX - clientX > 0) {
      this.loadNextPage();
    }
  }

  getCurrentPageNumber() {
    const {
      offset,
      pageSize,
    } = this.state;
    return Math.floor(offset / pageSize) + 1;
  }

  getCurrentPageItems() {
    const {
      offset,
      currentChunk,
      prevChunk,
      nextChunk,
      pageSize,
    } = this.state;

    if (!currentChunk) {
      return [];
    }

    const startOffset = prevChunk
      ? (prevChunk.chunkNumber * CHUNK_SIZE)
      : (currentChunk.chunkNumber * CHUNK_SIZE);
    const itemsOffset = offset - startOffset;
    const items = [];
    if (prevChunk) {
      items.push(...prevChunk.items);
    }
    items.push(...currentChunk.items);
    if (nextChunk) {
      items.push(...nextChunk.items);
    }

    return items.slice(itemsOffset, itemsOffset + pageSize);
  }

  render() {
    const slider = this.sliderRenderFunction({
      onMouseDown: this.onMouseDown,
      onMouseUp: this.onMouseUp,
      onTouchStart: this.onTouchStart,
      onTouchEnd: this.onTouchEnd,
      animationClass: this.state.animationClass,
      videoItems: this.getCurrentPageItems(),
    });

    if (this.slider !== slider && !this.isLoading) {
      this.component.replaceChild(slider, this.slider);
      this.slider = slider;
    }

    const paging = this.pagingRenderFunction({
      loadPrevPage: this.loadPrevPage,
      loadNextPage: this.loadNextPage,
      currentPage: this.getCurrentPageNumber(),
    });

    if (this.paging !== paging) {
      this.component.replaceChild(paging, this.paging);
      this.paging = paging;
    }

    return this.component;
  }
}

export default App;
