import videoBlock from './videoBlock';

const componentClassName = 'slider';
const blocksContainerClassName = `${componentClassName}__blocks-container`;

const slider = ({
  onMouseDown,
  onMouseUp,
  onTouchStart,
  onTouchEnd,
  videoItems,
  animationClass,
}) => {
  let clientX = 0;

  const component = document.createElement('div');
  component.classList.add(componentClassName);

  const blocksContainer = document.createElement('div');
  blocksContainer.classList.add(blocksContainerClassName, animationClass);

  component.addEventListener('mousedown', (e) => {
    ({ clientX } = e);
    onMouseDown(e);
  });
  component.addEventListener('mouseup', (e) => {
    blocksContainer.style.left = 0;
    onMouseUp(e);
  });
  component.addEventListener('mousemove', (e) => {
    if (e.buttons === 1) {
      blocksContainer.style.left = `${e.clientX - clientX}px`;
    }
  });
  component.addEventListener('touchstart', (e) => {
    const changedTouch = e.changedTouches[0];
    ({ clientX } = changedTouch);
    onTouchStart(e);
  });
  component.addEventListener('touchend', (e) => {
    blocksContainer.style.left = 0;
    onTouchEnd(e);
  });
  component.addEventListener('touchmove', (e) => {
    blocksContainer.style.left = `${e.changedTouches[0].clientX - clientX}px`;
  });

  if (videoItems) {
    videoItems.forEach(videoItem => blocksContainer.appendChild(videoBlock(videoItem)));
  }

  component.appendChild(blocksContainer);

  return component;
};

export default slider;
