
import { BLOCKS_PER_WIDTH } from '../settings/settings';

const getNumberOfBlocks = () => {
  const width = document.getElementsByTagName('body')[0].clientWidth;
  return BLOCKS_PER_WIDTH[Object.keys(BLOCKS_PER_WIDTH).find(key => width <= Number(key))]
    || BLOCKS_PER_WIDTH.default;
};

export default getNumberOfBlocks;
