import { nextIcon, prevIcon } from '../../settings/settings';

const componentClassName = 'paging';
const controlsBlockClassName = `${componentClassName}-controls`;
const pagingItemClassName = `${controlsBlockClassName}__item`;

const paging = ({ loadPrevPage, currentPage, loadNextPage }) => {
  const component = document.createElement('div');
  component.classList.add(componentClassName);

  const controlsBlock = document.createElement('div');
  controlsBlock.classList.add(controlsBlockClassName);

  const pagingItemPrev = document.createElement('span');
  pagingItemPrev.classList.add(pagingItemClassName, ...prevIcon);
  pagingItemPrev.addEventListener('click', loadPrevPage);

  const pagingItemCurrent = document.createElement('span');
  pagingItemCurrent.classList.add(pagingItemClassName);
  pagingItemCurrent.innerText = currentPage;

  const pagingItemNext = document.createElement('span');
  pagingItemNext.classList.add(pagingItemClassName, ...nextIcon);
  pagingItemNext.addEventListener('click', loadNextPage);

  controlsBlock.appendChild(pagingItemPrev);
  controlsBlock.appendChild(pagingItemCurrent);
  controlsBlock.appendChild(pagingItemNext);
  component.appendChild(controlsBlock);

  return component;
};

export default paging;
